import React from "react";
import { useTranslation } from 'react-i18next';
import {useCustomStore} from "../../context/useStore";
import {ACTIONS} from '../../context/providerContext';

import './index.scss';
import LeftSlider from './LeftSlider';
import * as R from "ramda";

import constructorImage from '../../assets/components/ConstructorSlider/result1.webp';


const ConstructorSlider = (props) => {
    const { state, dispatch } = useCustomStore();
    const { t } = useTranslation();

    const totalPrice = state.yourSet.basketYourSet ? R.compose(
        R.sum,
        R.pluck('priceAll')
    )(state.yourSet.basketYourSet) : 0;

    const totalWeight = state.yourSet.basketYourSet ? R.compose(
        R.sum,
        R.pluck('weightAll')
    )(state.yourSet.basketYourSet) : 0;

    const totalCountBasket = state.yourSet.basketYourSet ? R.compose(
        R.sum,
        R.pluck('countBasket')
    )(state.yourSet.basketYourSet) : 0;

    const rollsLength = state.yourSet.basketYourSet.reduce((acc, val) => acc + val.countBasket, 0);
    let share = 0;
    if ( rollsLength === 4 || rollsLength === 5 ) {
        share = 10;
    } else if ( rollsLength === 6 || rollsLength === 7 ) {
        share = 15;
    } else if ( rollsLength >= 8 ) {
        share = 20;
    }
    
    return (
        <div className="constructor-slider">
            <div className="content">
                {!state.yourSet.basketYourSet[0] && <div className="content_left">
                    <img
                    alt="Конструктор сета"
                    src={constructorImage} 
                    />
                </div>}
                <div className="content_right">
                    { state.yourSet.basketYourSet[0] && 
                    <h4>{t('components.constructorSlider.title')}</h4> 
                    }
                    <ul className={'content'}>
                        {
                            state.yourSet.basketYourSet.map((el, index) =>
                                <LeftSlider {...props} slider={el} key={index} />
                            )
                        }
                        {
                            !state.yourSet.basketYourSet[0] &&
                            <>
                                <p>Собери свой сет и получи скидку!</p>
                                <p className="bold">4-5 ролла 10%</p>
                                <p className="bold">6-7 ролла 15%</p>
                                <p className="bold">8-9 ролла 20%</p>
                            </>
                        }
                    </ul>
                </div>
            </div>
            <div className="text">
                <span>{t('components.constructorSlider.text')}</span>
                {share !== 0 && 
                <p>
                    При выборе такого количества роллов действует скидка 
                    <span style={{marginLeft:'5px'}}>
                        {share}%
                    </span> !
                </p>}
                <div className="text_paragraphs">
                    <p className="text_paragraphs_price">
                        {(totalPrice * (100 - share)/100).toFixed(0)}грн
                        {share !== 0 && <span>{totalPrice}грн</span>}
                    </p>
                    <p>/ {totalWeight}гр   /   {totalCountBasket}шт</p>
                </div>
                <button 
                    className={'open-modalTwo'}
                    onClick={() => dispatch({ 
                        type: ACTIONS.CONSTRUCT_YOUR_SET,
                        share: share,
                        array: state.yourSet.basketYourSet
                    })}
                >
                    {'В корзину'}
                </button>
            </div>
        </div>
    );
}

export default ConstructorSlider;