import React from 'react';
import { backendUrl } from '../../../config/config';
import Slider from "react-slick";
import {Link} from "react-router-dom";

import './index.scss';

const MainBlog = (props) => {

    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 4000
    };

    console.log(props.slider);

    return (
        <div className={'main-blog'}>
            <Slider {...settings}
                className={'slide-block'}
            >
                {
                    props.slider.map(post =>
                        <div
                            key={post.post._id}
                            className={'list'}
                        >
                            <div 
                                onClick={() => props.history.push(
                                    props.match.path.slice(0, 3) + `/posts/${post.post.route}`
                                )}
                                className={'list_left'}
                            >
                                <img src={backendUrl + '/' + post.post.image} alt=""/>
                            </div>
                            <div className={'list_right'}>
                                <div className={'top'}>
                                    <h3>{post.label.slice(0, 100)}</h3>
                                    <p>
                                        {post.description.slice(0, 200) + (post.description.length > 200 ? '...' : '')}
                                    </p>
                                </div>
                                <Link
                                    className={'bottom'}
                                    to={props.match.path.slice(0, 3) + `/posts/${post.post.route}`}
                                >
                                    <button>
                                        Блог
                                    </button>
                                </Link>
                            </div>

                        </div>
                    )
                }
            </Slider>
        </div>
    );
}

export default MainBlog;