import React, { useState } from 'react';
import Modal from 'react-modal';
import { backendUrl } from '../../config/config';
import CustomModal from '../UI/Modal/Modal';
import { Icon } from 'react-icons-kit';
import {ic_clear} from 'react-icons-kit/md/ic_clear';
import {shoppingBasket} from 'react-icons-kit/fa/shoppingBasket';
import { useTranslation } from 'react-i18next';
import {Link} from "react-router-dom";

import './index.scss';
import ListBasket from "./ListBasket";
import {useCustomStore} from "../../context/useStore";
import { ACTIONS } from '../../context/providerContext';
import Order from '../Order/Order';


const customStyles = {
    content : {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        transition: 'opacity 500ms ease-in-out',
        zIndex: '1000000 !important',
    }
};

Modal.setAppElement('#root')

const Basket = (props) => {
    const {t} = useTranslation();

    const [modalIsOpen,setIsOpen] = useState(false);
    const [ orderModalOpened, setOrderModalOpened ] = useState(false);
    const { state, dispatch } = useCustomStore();
    const openModal = () => {
        setIsOpen(true);
    }
    const closeModal = () => {
        setIsOpen(false);
    }

    const totalPrice = state.cart.basket.reduce(
        (acc, val) => acc + val.price * val.countBasket * (100 - val.percent)/100
        , 0);

    const totalConstructorSetPrice = 
        state.yourSet.orderSet
            .reduce((acc, val) => acc + (val.price * val.countBasket),0);

    return (
        <>
            {orderModalOpened && 
            <CustomModal
                forOrder
                show={ orderModalOpened }
                modalClosed={ () => {
                    setOrderModalOpened(false);
                    setIsOpen(true);
                } }
                onOrderSubmit={() => {
                    setOrderModalOpened(false);
                    setIsOpen(false);
                }}
            >
                <Order present={props.present} forModal />
            </CustomModal>}
            <button
                className={props.style}
                onClick={props.style === 'open-modal' || props.style === 'open-modalTwo'
                ? 
                () => dispatch({ type: ACTIONS.ADD_TO_BASKET_SOME_COUNT, payload: props.food, count: props.count}) 
                : 
                openModal}
                style={!props.title ? {width: 'auto'} : null }
            >
                { props.title ? 
                (props.smallMode ? 
                <div style={props.smallMode < 610 ? 
                {width:'15px',height:'15px'} : 
                {width:'20px',height:'20px'}}>
                    <Icon 
                    size={'100%'}
                    icon={shoppingBasket}
                    />
                </div> 
                : t(props.title)) 
                : <img src={props.src} alt="basket" height={props.smallMode ? 28 : '100%'} />}
            </button>
            {props.style === 'open-modalThree' && 
            <div className="open-modalThree_price">
                {state.yourSet.orderSet[0] ? state.cart.basket.length + 1 : state.cart.basket.length}
            </div>
            }
            <Modal
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <div className={'modal-block'}>
                    <button
                        className={'button-close'}
                        onClick={closeModal}
                    >
                        <Icon
                            size={'100%'}
                            icon={ic_clear}
                        />
                    </button>
                    <h2>
                        Корзина
                    </h2>
                    <div className={'modal-block_footer'}>
                        <div className={'left'}>
                            <p>{t('components.basket.total')}</p>
                            <h2>
                                {state.yourSet.orderSet[0] 
                                ? (totalPrice + totalConstructorSetPrice * (100 - state.yourSet.share)/100).toFixed(0) 
                                :totalPrice.toFixed(0)}
                            </h2>
                        </div>
                        {
                            (state.cart.basket[0] || state.yourSet.orderSet[0])  ? 
                                <button
                                    className={'right'}
                                    onClick={
                                        window.innerWidth < 1060
                                        ?
                                        () => {
                                            setIsOpen(false);
                                        } 
                                        : 
                                        () => {
                                            setIsOpen(false);
                                            setOrderModalOpened(true);
                                        }
                                    }
                                >   
                                    {window.innerWidth < 1060 ?
                                    <Link to={props.match.path.slice(0, 3) + '/main-mobile/order'}>
                                        {t('button.checkout')}
                                    </Link> : 
                                    t('button.checkout')}
                                </button> : null
                        }
                    </div>
                    <div className={'modal-block_content'}>
                        {
                            (state.cart.basket[0] || state.yourSet.orderSet[0]) ? 
                            state.cart.basket.map((el, index) =>  
                            <ListBasket key={index} list={el} />) 
                            : <p>{t('components.basket.cartEmpty')}</p>
                        }
                        {
                            state.yourSet.orderSet[0] &&
                                <>
                                <div style={{
                                    display: 'flex',
                                    justifyContent: 'space-between' ,
                                    alignItems: 'center'
                                }}>
                                    <p style={{fontSize:'14px'}}>
                                        {'Конструктор сета:'}
                                    </p>
                                    <div style={{display: 'flex', alignItems: 'center'}}>
                                        <p style={{fontWeight:'bold'}}>
                                            {(totalConstructorSetPrice * 
                                            (100 - state.yourSet.share)/100).toFixed(0)}грн.
                                            <span style={{
                                                fontWeight: 'normal',
                                                fontSize: '12px',
                                                textDecoration: 'line-through',
                                                position: 'relative',
                                                top: '-5px'
                                            }}>
                                                {totalConstructorSetPrice}грн.
                                            </span>
                                        </p>
                                        <div 
                                        onClick={() => dispatch({ type: ACTIONS.CLEAR_YOUR_SET })}
                                        style={{
                                            width:'20px', height:'20px', marginLeft:'10px',
                                            color: 'brown', cursor:'pointer',
                                            backgroundColor: '#eee', borderRadius: '50%'
                                        }}
                                        >
                                            <Icon
                                            size={'100%'}
                                            icon={ic_clear}
                                            />
                                        </div>
                                    </div>
                                </div>
                                {state.yourSet.orderSet.map(el => (
                                    <li>
                                        <div className={'left'} key={el._id}>
                                            <div className={'title-image'}>
                                                <img
                                                    src={backendUrl + `/${el.image}`}
                                                    alt={el.name_ru}
                                                />
                                            </div>
                                            <div className={'left_text'}>
                                                <h3>{el.name_ru}</h3>
                                                <p>{el.categoryId.name_ru}</p>
                                            </div>
                                        </div>
                                        <div className={'right'} key={'dr'}>
                                            <div key={'dr1'}>
                                                <span>
                                                    <p>{el.countBasket}шт.</p>
                                                </span>
                                            </div>
                                            <div
                                                key={'dr2'}
                                                className={'price'}
                                                style={{
                                                    marginLeft: '10px'
                                                }}
                                            >
                                                <span style={{textDecoration:'none', fontWeight:'bold'}}>
                                                    {el.price} грн.
                                                </span>
                                            </div>
                                        </div>
                                    </li>
                                ))}
                            </>
                        }
                    </div>
                </div>
            </Modal>
        </>
    )
}

export default Basket;