import React, { useEffect, useState, Suspense } from 'react';
import axios from 'axios';
import { backendUrl } from './config/config';
import { Switch, Route } from 'react-router-dom';
import store from 'store';
import Modal from './components/UI/Modal/Modal';
import ScrollToTop from './tools/ScrollToTop';
import ErrorBoundary from "./components/Errors/ErrorBoundary";
import ErrorIndicator from './components/Errors/ErrorIndicator/index';
import NotMatchPage from './components/Errors/NotMatchPage/NotMatchPage';
import Spinner from './components/Spinner/index';

//wrapper 
import Wrapper from './components/Wrapper/Wrapper';
//wrapper for mobile menu
import WrapperForMobile from './components/Wrapper/WrapperForMobile/WrapperForMobile';

//pages
import Main from './page/Main';
import Order from './page/Order/index';

//auth
import LogOut from './components/Auth/LogOut/LogOut';
import ConfirmAccount from './components/Auth/ConfirmAccount/ConfirmAccount';
import RecoverPassword from './components/Auth/RecoverPassword/RecoverPassword';

import './app.scss';

// lazy
const Delivery = React.lazy(() => import('./page/Delivery/index'));
const AboutUs = React.lazy(() => import('./page/AboutUs/index'));
const Shares = React.lazy(() => import('./page/Shares/index'));
const Favourite = React.lazy(() => import('./page/PageWrapper/Favourite'));
const BlogAll = React.lazy(() => import('./page/BlogAll/index'));
const BlogArticle = React.lazy(() => import('./page/BlogArticle/index'));

const PageWrapper = React.lazy(() => import('./page/PageWrapper'));
const CardProduct = React.lazy(() => import('./page/CardProduct'));

const MobileMenu = React.lazy(() => import('./page/MobileMenu/MobileMenu'));
const MobileOrder = React.lazy(() => import('./page/MobileOrder/MobileOrder'));
const MobilePhones = React.lazy(() => import('./page/MobilePhones/MobilePhones'));
const MobileSelectCity = React.lazy(() => import('./page/MobileSelectCity/MobileSelectCity'));

const Auth = React.lazy(() => import('./page/Auth/Auth'));


function App(props) {

    const [ isAuthorized, setAuthorized ] = useState(false);
    const [ city, setCity ] = useState(null);
    const [ lang, setLang ] = useState(null);
    const [ cityModal, setcityModal ] = useState(false);

    const [ breadProd, setBreadProd ] = useState([]);
    const [ breadPosts, setBreadPosts ] = useState([]);

    const [ categories, setCategories ] = useState(null);
    const [ filters, setFilters ] = useState(null);
    const [ presents, setPresents ] = useState([]);
    const [ seo, setSeo ] = useState([]);

    const [ error, setError ] = useState(false);
    const [ loading, setLoading ] = useState(false);

    useEffect(() => {
            setError(false);
            setLoading(true);

            //seo
            axios.get(backendUrl + '/seo/')
                .then(res => {
                    console.log(res.data.message);
                    setSeo(res.data.message);
                })
                .catch(err => {
                    setError(true);
                    setLoading(false);
            })

            //filters
            axios.get(backendUrl + '/products/filters')
            .then(res => {
                setFilters(res.data.fils)
                setError(false);
            })
            .catch(err => {
                setError(true);
                setLoading(false);
            });

            //presents
            axios.get(backendUrl + '/products/category/5ff17a7f6158c22e503feaa1')
            .then(res => {
                setPresents(res.data);
            })
            .catch(err => {
                setError(true);
                setLoading(false);
            });

            //categories
            axios.get(backendUrl + '/products/categories')
            .then(res => {
                setCategories(res.data.cats);
                setError(false);
                setLoading(false);
            })
            .catch(err => {
                setError(true);
                setLoading(false);
            });
    }, []);

    useEffect(() => {

        const data = store.get('24rolls');
        if ( data ) {
            if ( data.user && isAuthorized === false ) {
                setAuthorized(true);
            }
            if ( data.customOptions ) {
                switch ( data.customOptions.city ) 
                {
                    case 'Dnipro': {
                        if ( city !== 1 ) {
                            setCity(1);
                            props.onSetBaseName('/dp');
                        }
                        break;
                    }
                    case 'Zaporijja': {
                        if ( city !== 2 ) {
                            setCity(2);
                            props.onSetBaseName('/zp');
                        }
                        break;
                    }
                    case 'Kharkov': {
                        if ( city !== 3 ) {
                            setCity(3);
                            props.onSetBaseName('/kh');
                        }
                        break;
                    }
                    default: break;
                }
            }
        } else {
            setcityModal(true);
            setCity(1);
        }

        const language = store.get('i18nextLng') ? store.get('i18nextLng') : 'ru';
        if (!lang) {
            setLang(language);
        }

    }, [isAuthorized, city, props, lang]);

    useEffect(() => {
        if (props.location.search === '?redirectpersonal=true') {
            props.history.push(props.match.path.slice(0, 3) + '/order/personal');
        } else if ( props.location.search === '?log-up-success=true' ) {
            props.history.push(props.match.path.slice(0, 3) + '/order/personal?signup=true');
        } else if ( props.location.search === '?logout' ) {
            setAuthorized(false);
            props.history.push(props.match.path.slice(0, 3) + '/');
        }

    }, [props]);

    const onCityModalClosed = () => {
        setcityModal(false);
        setCity(1);
        props.onSetBaseName('/dp');
        store.set('24rolls', {
            customOptions : {
                city: 'Dnipro'
            } 
        });
    };

    const onSetCustomCity = (cityName) => {
        setcityModal(false);
        switch (cityName) {
            case 'Dnipro': {
                setCity(1);
                store.set('24rolls', {
                    customOptions : {
                        city: 'Dnipro'
                    } 
                });
                props.onSetBaseName('/dp');
                break;
            }
            case 'Zaporijja': {
                setCity(2);
                store.set('24rolls', {
                    customOptions : {
                        city: 'Zaporijja'
                    } 
                });
                props.onSetBaseName('/zp');
                break;
            }
            case 'Kharkov': {
                setCity(3);
                store.set('24rolls', {
                    customOptions: {
                        city: 'Kharkov'
                    }
                });
                props.onSetBaseName('/kh');
                break;
            }
            default: break;
        }
    };

    const path = props.match.path.slice(0, 3);

    //routes for big screens
    const routesBig = (
        <ErrorBoundary>
        <ScrollToTop />
        <Switch>
            {!cityModal && 
            <Route exact path={path + "/"} render={(props) => 
                <Main 
                    {...props}
                    city={city}
                    categories={categories}
                    seo={
                        props.match.path.slice(0, 3) === '/kh' ?
                        seo.filter(el => el.name === 'Kh')[0] :
                        (
                            props.match.path.slice(0, 3) === '/zp' ?
                            seo.filter(el => el.name === 'Zp')[0] :
                            (
                                props.match.path.slice(0, 3) === '/dp' ?
                                seo.filter(el => el.name === 'Dp')[0] :
                                seo.filter(el => el.name === 'Menu')[0]
                            )
                        )
                    }
                />
            }/>}
            <Route exact path={path + "/shares"} render={() => (
                <Suspense fallback={<Spinner />}>
                    <Shares seo={seo.filter(el => el.name === 'Actions')[0]} />
                </Suspense>
            )} />
            <Route exact path={path + "/about-us"} render={() => (
                <Suspense fallback={<Spinner />}>
                    <AboutUs seo={seo.filter(el => el.name === 'Contacts')[0]} />
                </Suspense>
            )} />
            <Route exact path={path + "/delivery"} render={() => (
                <Suspense fallback={<Spinner />} >
                    <Delivery seo={seo.filter(el => el.name === 'Delivery')[0]} />
                </Suspense>
            )} />
            <Route exact path={path + "/favourite"} render={() =>(
                <Suspense fallback={<Spinner />} >
                    <Favourite  {...props} title={'title.favourite'} />   
                </Suspense> 
            )} />
            <Route path={path + '/contacts'} exact render={() =>
                <Suspense fallback={<Spinner />}>
                    <MobilePhones />
                </Suspense>
            } />
            <Route path={path + '/change-city'} exact render={() =>
                <Suspense fallback={<Spinner />}>
                    <MobileSelectCity 
                    {...props}
                    city={city}
                    setCity={setCity}
                    onSetBaseName={props.onSetBaseName} 
                    />
                </Suspense>
            } />

            <Route path={path +  "/main-mobile/menu"} exact render={() => 
                <Suspense fallback={<Spinner />}>
                    <MobileMenu {...props}/>
                </Suspense>
            }/>
            <Route path={path + "/main-mobile/order"} exact render={() => 
                <Suspense fallback={<Spinner />}>
                    <MobileOrder {...props} presents={presents}/>
                </Suspense>
            } />
            <Route 
            exact
            path={path + "/main-mobile/log-in"} 
            render={(props) => 
                <Suspense fallback={<Spinner />}>
                    <Auth {...props}/>
                </Suspense>
            } 
            />

            {/* Категории */}
            {categories && filters && categories.map(el => {
              if (el._id === '5ff17a7f6158c22e503feaa1')  return null;
              else return (
                <Route 
                exact
                key={el._id}
                path={path + '/' + el.route} 
                render={() => (
                    <Suspense fallback={<Spinner />} >
                        <PageWrapper 
                        {...props}
                        title={el.name_ru} 
                        filters={filters}
                        categoryId={el._id} 
                        setBreadProd={setBreadProd}
                        el={el}
                        url={`/products/category/${el._id}`} 
                        /> 
                    </Suspense>
                )} 
                />
              );
            })}

            {/* Фильтры */}
            {filters && filters.map(el => (
               <Route
               exact
               key={el._id}
               path={path + '/' + el.route}
               render={() => (
                   <Suspense fallback={<Spinner />}>
                        <PageWrapper 
                        {...props} 
                        el={el}
                        title={el.name_ru} 
                        setBreadProd={setBreadProd}
                        url={`/products/filter/${el._id}`} 
                        />
                   </Suspense>
               )} 
               /> 
            ))}

            <Route exact path={path + "/posts/:route"} render={(props) => 
                <Suspense fallback={<Spinner />}>
                    <BlogArticle {...props} />
                </Suspense>
            }/>
            <Route exact path={path + "/posts"} render={() => 
                <Suspense fallback={<Spinner />}>
                    <BlogAll 
                        setBreadPosts={setBreadPosts}
                        seo={seo.filter(el => el.name === 'Blog')[0]} {...props} 
                    />
                </Suspense>
            }/>
            { isAuthorized ? 
            ([
                <Route 
                key='3-route'
                path={path + "/order"}
                component={Order} />,
                <Route
                exact
                key='4-route'
                path={path + "/logout"}
                component={LogOut} 
                />
            ])   
            :
            (
                <Route 
                key='1-route'
                exact
                path={path + "/log-in"} 
                render={(props) => 
                    <Suspense fallback={<Spinner />}>
                        <Auth {...props}/>
                    </Suspense>
                } 
                />
            )}
            <Route exact path={path + "/:catRoute/:route"} render={(props) => 
                <Suspense fallback={<Spinner />}>
                    <CardProduct {...props} />
                </Suspense>
            }/>
            <Route component={NotMatchPage} />
        </Switch>
        </ErrorBoundary>
    );
       
    //global routes
    const routes = (
        <Switch>
            <Route exact path={path + "/signup/confirm/:token"} component={ConfirmAccount} />
            <Route exact path={path + "/reset/toUser/:token"} component={RecoverPassword} />
            <Route render={() => {
                return (
                    <Wrapper
                    {...props}
                    categories={categories}
                    filters={filters}
                    presents={presents}
                    onSetBaseName={props.onSetBaseName}

                    breadProd={breadProd}
                    breadPosts={breadPosts}

                    isAuthorized={isAuthorized}
                    city={city}
                    lang={lang}
                    setCity={setCity}
                    setLang={setLang}>
                        {routesBig}
                    </Wrapper>
                );
            }}/>
        </Switch>
    );

    const modalContent = 
    <div className="modal-for-change-start-city">
        <p>
            {'Вы из Днепра?'}
            <span onClick={()=>onSetCustomCity('Dnipro')}>
                {'Да'}
            </span>
        </p>
        <p>
            {'Выбрать другой город:'}
        </p>
        <ul>
            <li 
                onClick={()=>onSetCustomCity('Zaporijja')}
            >
                {'Запорожье'}
            </li>
            <li 
                style={{marginTop: '10px'}}
                onClick={()=>onSetCustomCity('Kharkov')}
            >
                {'Харьков'}
            </li>
        </ul>
    </div>

    return (<>  
            { error && !loading && <ErrorIndicator /> }
            { !error && 
                <>
                    <Modal
                        show={cityModal}
                        modalClosed={onCityModalClosed}
                    >
                        {modalContent}
                    </Modal>
                    {routes}
                </>}
        </>)
}

export default App;