import React from 'react';
import { backendUrl } from '../../../../config/config';

const ShareDiv = (props) => (
    <div style={{
        width: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        marginTop: '40px'
    }}>
        <img 
            src={backendUrl + '/' + props.src}
            alt="Акция"
            style={{
                maxWidth: '95%', 
                maxHeight: '600px',
                borderRadius: '15px'
            }}
        />
    </div>
);

export default ShareDiv;