import React from "react";
import axios from "axios";
import store from "store";
import NumberFormat from 'react-number-format';
import { withTranslation } from "react-i18next";
import { withRouter } from "react-router-dom";
import { backendUrl } from "../../config/config";
import { Icon } from "react-icons-kit";
import { ic_clear } from "react-icons-kit/md/ic_clear";

import { storeContext, ACTIONS } from "../../context/providerContext";
import Spinner from "../Spinner/index";
import Modal from "../UI/Modal/Modal";

import "./order.scss";
import "../../page/MobileOrder/mobileorder.scss";

//icon
import cake from "../../assets/components/Wrapper/RightMenu/cake.png";
import select from '../../assets/components/Wrapper/RightMenu/select.png';

class Order extends React.Component {
  static contextType = storeContext;

  state = {
    form: {
      name: "",
      phone: "",
      address: "",
      flat: "",
      entrance: "",
      floor: "",
      intercom: "",
      comment: "",
      payBack: '',
      email: ''
    },
    payType: "cash",
    deliveryType: 'courier',
    beskontaktnaya: false,
    tools: true,
    toolsCount: 0,
    bonusVisible: false,
    bonusSum: 0,
    agree: false,
    error: null,
    loading: false,
    modalOpened: false,
    orderMode: false,
    ordersClosed: false,
    token: store.get("24rolls")
      ? store.get("24rolls").user
        ? store.get("24rolls").user.token
        : null
      : null,
  };

  componentDidMount() {
    if (this.props.forMobile || this.props.forModal) {
      this.setState({ orderMode : true });
    }

    if (this.state.token) {

      axios.get(backendUrl + '/user/', {
        headers: {
            'Authorization': `Bearer ${this.state.token}`
        }
      })
      .then(res => {
        if (res.data.orders[0]) {
          this.setState({
            error: false,
            loading: false,
            form: {
              ...this.state.form,
              name: res.data.user.name,
              phone: res.data.user.phoneNumber,
            }
          });
        }
      })
      .catch(err => {
          this.setState({
            error: true,
            loading: false
          });
      });

    }
  }

  onSelectChange = (event) => {
    this.setState((prevState) => ({
      payType: event.target.value,
      beskontaktnaya:
        event.target.value === "cash" ? false : prevState.beskontaktnaya,
    }));
  };

  onBonusChange = (event) => {
    this.setState({
      bonusSum: event.target.value,
    });
  };

  onInputChangeHandler = (event, id) => {
    const updatedForm = {
      ...this.state.form,
    };
    updatedForm[id] = event.target.value;
    this.setState({ form: updatedForm });
  };

  onOrderSubmit = (event, present, constructorSum, share) => {
    event.preventDefault();

    this.setState({ loading: true });
    const { state, dispatch } = this.context;
    const { t } = this.props;
    let bonus = 0;

    const defaultState = {
      form: {
        name: "",
        phone: "",
        address: "",
        flat: "",
        entrance: "",
        floor: "",
        intercom: "",
        comment: "",
        payBack: '',
        email: ''
      },
      payType: "cash",
      beskontaktnaya: false,
      tools: true,
      toolsCount: 0,
      bonusVisible: false,
      bonusSum: 0,
      agree: false,
      error: null,
      loading: false,
      modalOpened: true,
      orderMode: false,
      ordersClosed: false,
      token: store.get("24rolls").user ? store.get("24rolls").user.token : null,
    };

    const currentTime = ((new Date()).toLocaleTimeString()).split(':')[0];
    if ( currentTime >= 3 && currentTime <=9 ) {
      this.setState({ 
        ordersClosed: true,
        loading: false,
        error: false
       });
    } else {

      let items = state.cart.basket.map((el) => ({
        product: el.id,
        quantity: el.countBasket
      }));
      const constructor = state.yourSet.orderSet.map((el) => ({
        product: el._id,
        quantity: el.countBasket
      }));
      items = items.concat(constructor);
      if (present) {
        items.push({
          product: present._id,
          quantity: 1,
        });
      }
      const token = store.get("24rolls").user
        ? store.get("24rolls").user.token
        : null;
  
      // basket is exist
      if (!state.cart.basket[0] && !state.yourSet.orderSet[0]) {
        this.setState({
          error: t("order.orderOffer.errorBasket"),
          loading: false,
        });
      }
      // user not authorized
      else if (!token) {
        axios
          .post(backendUrl + "/order/notAuth", {
            oplata: {
              type: this.state.payType,
              status: "not-paid",
            },
            delivery: {
              status: this.state.deliveryType,
            },
            city: store.get("24rolls").customOptions.city,
            address: {
              text:
                this.state.form.address +
                "\n" +
                "кв/офис: " +
                this.state.form.flat +
                "\n" +
                "подьезд: " +
                this.state.form.entrance +
                "\n" +
                "этаж: " +
                this.state.form.floor +
                "\n" +
                "домофон: " +
                this.state.form.intercom,
              city: store.get("24rolls").customOptions.city,
            },
            name: this.state.form.name,
            phone: this.state.form.phone,
            comment: this.state.form.comment,
            sdachaS: this.state.form.payBack.trim() === '' ? 0 : this.state.form.payBack,
            cutlery: this.state.toolsCount,
            bonus: (constructorSum - (constructorSum * (100 - share)/100)).toFixed(0),
            items: items,
            email: this.state.form.email
          })
          .then((res) => {
            this.setState(defaultState);
            dispatch({ type: ACTIONS.CLEAN_BASKET });
            this.setState({ loading: false, error: false });
          })
          .catch((err) => {
            this.setState({
              error: true,
              loading: false,
            });
          });
      }
      // user authorized
      else if (token) {
        axios
          .get(backendUrl + "/user/bonus", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          })
          .then((res) => {
            if (res.data.bonus >= this.state.bonusSum) {
              bonus = this.state.bonusSum;
              return bonus;
            } else {
              this.setState({
                loading: false,
                error: t("order.orderOffer.errorBonuses"),
              });
              return 0;
            }
          })
          .then((bonus) => {
            axios
              .post(
                backendUrl + "/order",
                {
                  oplata: {
                    type: this.state.payType,
                    status: "not-paid",
                  },
                  delivery: {
                    status: this.state.deliveryType,
                  },
                  city: store.get("24rolls").customOptions.city,
                  address: {
                    text:
                      this.state.form.address +
                      "\n" +
                      "кв/офис: " +
                      this.state.form.flat +
                      "\n" +
                      "подьезд: " +
                      this.state.form.entrance +
                      "\n" +
                      "этаж: " +
                      this.state.form.floor +
                      "\n" +
                      "домофон: " +
                      this.state.form.intercom,
                  },
                  name: this.state.form.name,
                  phone: this.state.form.phone,
                  cutlery: this.state.toolsCount,
                  sdachaS: this.state.form.payBack.trim() === '' ? 0 : this.state.form.payBack,
                  comment: this.state.form.comment,
                  bonusSum: bonus,
                  bonus: (constructorSum - (constructorSum * (100 - share)/100)).toFixed(0),
                  items: items,
                },
                {
                  headers: {
                    Authorization: `Bearer ${token}`,
                  },
                }
              )
              .then((res) => {
                if (bonus) {
                  dispatch({
                    type: ACTIONS.SET_BONUS_AMOUNT,
                    bonus: state.bonus - bonus,
                  });
                }
                dispatch({ type: ACTIONS.CLEAN_BASKET });
                this.setState(defaultState);
                this.setState({
                  error: false,
                  loading: false,
                });
              })
              .catch((err) => {
                this.setState({
                  error: err.response.data.message,
                  loading: false,
                });
              });
          })
          .catch((err) => {
            this.setState({ error: err.response.data.message, loading: false });
          });
      }

    }

  };

  render() {

    const { t } = this.props;
    const { state, dispatch } = this.context;

    const totalPrice = state.cart.basket.reduce(
      (acc, val) =>
        acc + (val.price * val.countBasket * ((100 - val.percent)/ 100)),
      0
    );

    const rollsLength = state.yourSet.orderSet.reduce(
      (acc, val) => acc + val.countBasket,
      0
    );

    let share = 0;
    if (rollsLength === 4 || rollsLength === 5) {
      share = 10;
    } else if (rollsLength === 6 || rollsLength === 7) {
      share = 15;
    } else if (rollsLength >= 8) {
      share = 20;
    }

    let totalConstructorSetPrice =
      this.context.state.yourSet.orderSet.reduce(
        (acc, val) => acc + Number((val.price * val.countBasket).toFixed(0)), 0);

    const totalSum = (totalPrice + totalConstructorSetPrice * (100 - share) / 100).toFixed(0);

    let present = null;
    if (this.props.presents) {
      if (totalSum >= 400 && totalSum < 600) {
        present = this.props.presents.filter(
          (el) => el.filter ? el.filter._id === "6012924b205af05787df2f6d" : false
        )[0];
      } else if (totalSum >= 600 && totalSum < 1000) {
        present = this.props.presents.filter(
          (el) => el.filter ? el.filter._id === "60129301205af05787df2f6e" : false
        )[0];
      } else if (totalSum >= 1000) {
        present = this.props.presents.filter(
          (el) => el.filter ? el.filter._id === "60129318205af05787df2f6f" : false
        )[0];
      }
    }

    const basketIsExist = state.cart.basket[0] || state.yourSet.orderSet[0];

    let maxBonuses = state.bonus;
    if (maxBonuses > 0.5 * totalSum) {
      maxBonuses = 0.5 * totalSum;
    }

    const disabledButtonStyle = {
      color: "rgba(0, 0, 0, 0.26)",
      backgroundColor: "#e0e0e0",
    };

    const token = store.get("24rolls")
      ? store.get("24rolls").user
        ? store.get("24rolls").user.token
        : null
      : null;

    return (
      <>
        <Modal
          show={this.state.modalOpened}
          modalClosed={() => {
            this.setState({ modalOpened: false });
            if (this.props.forMobile) {
              this.props.history.push(this.props.match.path.slice(0, 3) + "/");
            }
          }}
        >
          <div style={{ textAlign: "center" }}>
            <p
              style={{
                color: "green",
                textAlign: "center",
                fontWeight: "700",
                fontSize: "24px",
              }}
            >
              {t("order.orderOffer.processing")}
            </p>
          </div>
        </Modal>
        <Modal
          show={this.state.ordersClosed}
          modalClosed={() => this.setState({ordersClosed: false})} 
        >
          <p style={{textAlign:'center'}}>
            {'С 3:00 до 9:00 утра по Киевскому времени прием заказов не доступен! Пожалуйста обратитесь в другое время.'}
          </p>
        </Modal>
        <div
          className={this.props.forMobile ? "mobile-order" : "right-customs"}
          style={
            this.props.forModal
              ? {
                  width: "90%",
                }
              : {
                width: '100%'
              }
          }
        >
          {this.props.forMobile && (
            <h2 style={{ marginBottom: "0" }}> твой заказ </h2>
          )}
          <div
            className="right-customs__bottom"
            style={
              this.props.forMobile ? { width: "90%", maxWidth: '600px', boxShadow: "none" } : {}
            }
          >
            <div className="right-customs__bottom__title">
              <img src={cake} alt="img" width={45} height={47} />
              {basketIsExist && (
                <div className="right-customs__bottom__title__price">
                  {totalSum}₴
                </div>
              )}
              {!basketIsExist && (
                <p style={{ margin: "10px 0" }}>
                  {t("components.menu.right.4")}
                </p>
              )}
              {basketIsExist && (
                <div className="right-customs__bottom__title_container">
                  <ul style={{margin:'0'}}>
                    {this.context.state.cart.basket.map((el) => (
                      <li key={el.id}>
                        <div className={"left"} key={"dl"}>
                          <div className={"title-image"}>
                            <img
                              src={backendUrl + `/${el.image}`}
                              alt={el.alt}
                            />
                          </div>
                          <div className={"left_text"}>
                            <h3>{el.name_ru}</h3>
                            <div className={"right"}>
                              <button
                                onClick={() =>
                                  dispatch({
                                    type: ACTIONS.REMOVE_TO_BASKET,
                                    payload: el._id,
                                  })
                                }
                              >
                                -
                              </button>
                              <span>{el.countBasket}</span>
                              <button
                                onClick={() =>
                                  dispatch({
                                    type: ACTIONS.ADD_TO_BASKET,
                                    payload: el,
                                  })
                                }
                              >
                                +
                              </button>
                              <div 
                              className="price"
                              style={{
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center'
                              }}
                              >
                                {el.percent !== 0 && 
                                <span style={{
                                    textDecoration: 'line-through',
                                    fontWeight: 'normal'
                                  }}>
                                  {el.countBasket * el.price}грн.
                                </span>
                                }
                                <span>
                                  {(el.countBasket *
                                    el.price *
                                    ((100 - el.percent) / 100)).toFixed(0)}{" "}
                                  грн.
                                </span>
                              </div>
                            </div>
                          </div>
                        </div>
                      </li>
                    ))}
                  </ul>
                  {this.context.state.yourSet.orderSet[0] && (
                    <>
                      <div
                        style={{
                          display: "flex",
                          justifyContent: "space-between",
                          alignItems: "center",
                        }}
                      >
                        <p style={{ fontSize: "14px" }}>
                          {"Конструктор сета:"}
                        </p>
                        <div style={{ display: "flex", alignItems: "center" }}>
                          <p style={{ fontWeight: "bold", display:'flex', flexDirection: 'column' }}>
                            <span
                              style={{
                                fontWeight: "normal",
                                fontSize: "12px",
                                textDecoration: "line-through",
                                position: "relative",
                                top: '10px'
                              }}
                            >
                              {totalConstructorSetPrice}грн.
                            </span>
                            {(
                              (totalConstructorSetPrice *
                                (100 - state.yourSet.share)) /
                              100
                            ).toFixed(0)}
                            грн.
                          </p>
                          <div
                            onClick={() =>
                              dispatch({ type: ACTIONS.CLEAR_YOUR_SET })
                            }
                            style={{
                              width: "20px",
                              height: "20px",
                              marginLeft: "10px",
                              color: "brown",
                              cursor: "pointer",
                              backgroundColor: "#eee",
                              borderRadius: "50%",
                            }}
                          >
                            <Icon size={"100%"} icon={ic_clear} />
                          </div>
                        </div>
                      </div>
                      <ul style={this.props.forModal ? {margin:'10px 0'} : {margin: '0'}}>
                        {this.context.state.yourSet.orderSet.map((el) => (
                          <li key={el.id}>
                            <div className={"left"} key={"dl"}>
                              <div className={"title-image"}>
                                <img
                                  src={backendUrl + `/${el.image}`}
                                  alt={el.alt}
                                />
                              </div>
                              <div className={"left_text"}>
                                <h3>{el.name_ru}</h3>
                                <div className={"right"}>
                                  <span>{el.countBasket}шт.</span>
                                  <div 
                                  className="price" 
                                  style={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    alignItems: 'center'
                                  }}>
                                    <span style={{
                                      textDecoration: 'line-through',
                                      fontWeight: 'normal'
                                    }}>
                                      {el.countBasket * el.price}грн.
                                    </span>
                                    <span>
                                      {(el.countBasket *
                                        el.price *
                                        ((100 - share) / 100)).toFixed(0)}{' '}
                                      грн.
                                    </span>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </li>
                        ))}
                      </ul>
                    </>
                  )}
                  {present && (
                    <>
                      <p style={{ margin: "0" }}>{"Ваш подарок:"}</p>
                      <ul>
                        <li style={{ height: "70px" }}>
                          <div className={"left"} key={"dl"}>
                            <div className={"title-image"}>
                              <img
                                src={backendUrl + `/${present.image}`}
                                alt={present.alt}
                              />
                            </div>
                            <div
                              className={"left_text"}
                              style={{
                                display: "flex",
                                alignItems: "center",
                                padding: "0",
                                paddingLeft: "20px",
                              }}
                            >
                              <h3>{present.name_ru}</h3>
                            </div>
                          </div>
                        </li>
                      </ul>
                    </>
                  )}
                  {!this.state.orderMode && (
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "center",
                        marginBottom: "20px",
                        width: "100%",
                      }}
                    >
                      <button
                        style={{
                          margin: "0",
                          height: "40px",
                          padding: "5px 15px",
                          width: "80%",
                          marginTop: '10px'
                        }}
                        onClick={() => this.props.history.push(
                          this.props.match.path.slice(0, 3) + '/main-mobile/order'
                        )}
                      >
                        {"Оформить заказ"}
                      </button>
                    </div>
                  )}
                </div>
              )}
            </div>
            {this.state.orderMode && (
              <form className="right-customs__bottom__form">
                <div className="right-customs__bottom__form__name">
                  <input
                    required
                    type="text"
                    placeholder={t("components.menu.right.2")}
                    value={this.state.form.name}
                    onChange={(e) => this.onInputChangeHandler(e, "name")}
                  />
                  {!token && (
                    <>
                    <NumberFormat 
                    format="+38(0##) ### ## ##" 
                    mask="_"
                    allowEmptyFormatting
                    value={this.state.form.phone}
                    onChange={(e) => this.onInputChangeHandler(e, "phone")}
                    />
                    <input
                      style={{marginBottom:'10px'}}
                      required
                      type="email"
                      placeholder="Введите email"
                      value={this.state.form.email}
                      onChange={(e) => this.onInputChangeHandler(e, "email")}
                    />
                    </>
                  )}
                  <input
                    required
                    type="text"
                    placeholder={t("components.menu.right.5")}
                    value={this.state.form.address}
                    onChange={(e) => this.onInputChangeHandler(e, "address")}
                  />
                </div>
                <div className="right-customs__bottom__form__address">
                  <div className="right__bottom__form__address--item">
                    <p>{t("components.menu.right.6")}</p>
                    <input
                      type="text"
                      value={this.state.form.flat}
                      onChange={(e) => this.onInputChangeHandler(e, "flat")}
                    />
                  </div>
                  <div className="right-customs__bottom__form__address--item">
                    <p>{t("components.menu.right.7")}</p>
                    <input
                      type="text"
                      value={this.state.form.entrance}
                      onChange={(e) => this.onInputChangeHandler(e, "entrance")}
                    />
                  </div>
                  <div className="right-customs__bottom__form__address--item">
                    <p>{t("components.menu.right.8")}</p>
                    <input
                      type="text"
                      value={this.state.form.floor}
                      onChange={(e) => this.onInputChangeHandler(e, "floor")}
                    />
                  </div>
                  <div className="right-customs__bottom__form__address--item">
                    <p>{t("components.menu.right.9")}</p>
                    <input
                      type="text"
                      value={this.state.form.intercom}
                      onChange={(e) => this.onInputChangeHandler(e, "intercom")}
                    />
                  </div>
                </div>
                <div className="right-customs__bottom__form__delivery">
                  <input
                    disabled={this.state.payType === "cash"}
                    checked={this.state.beskontaktnaya}
                    onChange={() => {
                      this.setState((prevState) => ({
                        beskontaktnaya: !prevState.beskontaktnaya,
                      }));
                    }}
                    type="checkbox"
                    id={"desk-beskon"}
                  />
                  <label htmlFor={"desk-beskon"}>
                    <span> {t("components.menu.right.10")} </span>
                  </label>
                </div>
                <div className="right-customs__bottom__form__option">
                  <div className="select">
                    <select
                      value={this.state.payType}
                      onChange={(e) => this.onSelectChange(e)}
                    >
                      <option value={"cash"}>
                        {t("components.menu.right.11")}
                      </option>
                      <option value={"bank-card"}>
                        {t("components.menu.right.12")}
                      </option>
                    </select>
                    <img src={select} />
                  </div>
                  {this.state.payType === "cash" && (
                    <input
                      required
                      value={this.state.form.payBack}
                      onChange={(e) =>
                        this.setState({
                          form: {
                            ...this.state.form,
                            payBack: e.target.value,
                          },
                        })
                      }
                      type="text"
                      placeholder={t("components.menu.right.14")}
                    />
                  )}
                  <div 
                    className="select" 
                    style={{
                      marginTop:'20px',
                      borderBottomLeftRadius: '8px',
                      borderBottomRightRadius: '8px'
                    }}
                    >
                    <select
                      value={this.state.deliveryType}
                      onChange={(e) => this.setState({deliveryType: e.target.value})}
                    >
                      <option value={"courier"}>
                        {t("Доставка курьером")}
                      </option>
                      <option value={'self-delivery'}>
                        {t("Самовывоз -15%")}
                      </option>
                    </select>
                    <img src={select} />
                  </div>

                  <div className="right-customs__bottom__form__bonuses-use">
                    <div className="right-customs__bottom__form__bonuses-use__switcher">
                      <input
                        type="checkbox"
                        checked={this.state.bonusVisible}
                        onChange={() =>
                          this.setState((prevState) => ({
                            bonusVisible: !prevState.bonusVisible,
                          }))
                        }
                      />
                      <span>{t("order.orderOffer.bonusesUse")}</span>
                    </div>
                    <div
                      className="right-customs__bottom__form__bonuses-use__slidecontainer"
                      style={{
                        opacity: this.state.bonusVisible ? 1 : 0,
                        height: this.state.bonusVisible ? "100%" : "0px",
                      }}
                    >
                      {token ? (
                        <>
                          <input
                            onChange={(e) => {
                              this.onBonusChange(e);
                            }}
                            type="range"
                            min="0"
                            max={maxBonuses}
                            value={this.state.bonusSum}
                            className="slider"
                          />
                          <div className="right-customs__bottom__form__bonuses-use__slidecontainer__text">
                            <p
                              style={{
                                flex: "0 1 30%",
                              }}
                            >
                              <span style={{ fontWeight: "700" }}>
                                {this.state.bonusSum}
                              </span>{" "}
                              грн
                            </p>
                            <p
                              style={{
                                flex: "0 1 70%",
                                textAlign: "right",
                                fontSize: "12px",
                                margin: "0",
                              }}
                            >
                              {t("order.orderOffer.bonusesAmount")}
                            </p>
                          </div>
                        </>
                      ) : (
                        <p style={{ margin: "0" }}>
                          {t("order.orderOffer.enabledBonuses")}
                        </p>
                      )}
                    </div>
                  </div>

                  <div className="devices_input_disabled">
                    <span>{t("components.menu.right.15")}</span>
                    {this.state.tools && (
                      <div className="devices_input_disabled__tools">
                        <button
                          onClick={(e) => {
                            e.preventDefault();
                            this.setState((prevState) => ({
                              toolsCount:
                                prevState.toolsCount !== 0
                                  ? prevState.toolsCount - 1
                                  : 0,
                            }));
                          }}
                        >
                          -
                        </button>
                        <input
                          value={this.state.toolsCount}
                          onChange={(e) =>
                            this.setState({
                              toolsCount: Number(e.target.value),
                            })
                          }
                        />
                        <button
                          onClick={(e) => {
                            e.preventDefault();
                            this.setState((prevState) => ({
                              toolsCount: prevState.toolsCount + 1,
                            }));
                          }}
                        >
                          +
                        </button>
                      </div>
                    )}
                  </div>
                </div>
                <div className="right-customs__bottom__form__checkbox">
                  <input
                    checked={!this.state.tools}
                    onChange={() => {
                      this.setState((prevState) => ({
                        tools: !prevState.tools,
                        toolsCount: 0,
                      }));
                    }}
                    type="checkbox"
                    id={"desk-cutlery"}
                  />
                  <label htmlFor={"desk-cutlery"}>
                    <span>{t("components.menu.right.16")}</span>
                  </label>
                </div>
                <div className="right-customs__bottom__form__area">
                  <textarea
                    value={this.state.form.comment}
                    onChange={(e) => this.onInputChangeHandler(e, "comment")}
                    style={{
                      resize: "vertical",
                      minHeight: "30px",
                    }}
                    placeholder={t("components.menu.right.17")}
                  />
                </div>
                <div className="right-customs__bottom__form__area__check">
                  <input
                    checked={this.state.agree}
                    onChange={() => {
                      this.setState((prevState) => ({
                        agree: !prevState.agree,
                      }));
                    }}
                    type="checkbox"
                    id={"desk-agree"}
                  />
                  <label htmlFor={"desk-agree"}>
                    <p>{t("components.menu.right.18")}</p>
                  </label>
                </div>
                {this.state.error && (
                  <p className="right-customs__bottom__error-paragraph">
                    {this.state.error}
                  </p>
                )}
                <div style={{ display: "flex", justifyContent: "center" }}>
                  {this.state.loading ? (
                    <Spinner />
                  ) : (
                    <button
                      type="submit"
                      onClick={(e) => this.onOrderSubmit(e, present, totalConstructorSetPrice, share)}
                      disabled={
                        !(this.state.agree && this.state.form.name.trim() !== '' && this.state.form.phone.trim() !== '' && this.state.form.address.trim() !== '')
                      }
                      style={
                        !(this.state.agree && this.state.form.name.trim() !== '' && this.state.form.phone.trim() !== '' && this.state.form.address.trim() !== '') ? disabledButtonStyle : null
                      }
                    >
                      {t("button.checkout")}
                    </button>
                  )}
                </div>
              </form>
            )}
          </div>
        </div>
      </>
    );
  }
}

export default withRouter(withTranslation()(Order));
