import React from 'react';
import { backendUrl } from '../../config/config';

import { ACTIONS} from '../../context/providerContext';
import {useCustomStore} from "../../context/useStore";

const ListBasket = ({list}) => {
    const {dispatch} = useCustomStore();
    return (
        <li key={list._id}>
            <div className={'left'} key={'dl'}>
                <div className={'title-image'}>
                    <img
                        src={backendUrl + `/${list.image}`}
                        alt={list.name_ru}
                    />
                </div>
                <div className={'left_text'}>
                    <h3>{list.name_ru}</h3>
                    <p>{list.categoryId.name_ru}</p>
                </div>
            </div>
            <div className={'right'}>
                <div className={'right_text'}>
                    <button
                        onClick={() => dispatch({type: ACTIONS.REMOVE_TO_BASKET, payload: list._id})}
                    >
                        -
                    </button>
                    <span><p>{list.countBasket}</p></span>
                    <button
                        onClick={() => dispatch({type: ACTIONS.ADD_TO_BASKET, payload: list})}
                    >
                        +
                    </button>
                </div>
                <div className={'price'}>
                    {list.percent !== 0 && <span>{list.price}грн.</span>}
                    <p>
                        {(list.price * (100 - list.percent)/100).toFixed(0)} грн.
                    </p>
                </div>
            </div>
        </li>
    )
}

export default ListBasket;