import React, {Suspense } from 'react';
import ReactDOM from 'react-dom';
import WebFont from 'webfontloader';
import { BrowserRouter, Route, Redirect } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
import './index.scss';
import App from './App';
import reportWebVitals from './reportWebVitals';
import {StateProvider} from "./context/providerContext";
import store from 'store';
import './localization/i18n';

WebFont.load({
    google: {
      families: ['Roboto:300,400,700', 'sans-serif']
    }
  });

const Root = withRouter((props) => {

    const city = store.get('24rolls') ? store.get('24rolls').customOptions.city : null;

    let defaultBaseUrl;
    if ( city === 'Dnipro' ) {
        defaultBaseUrl = '/dp';
    } else if ( city === 'Zaporijja' ) {
        defaultBaseUrl = '/zp';
    } else if ( city === 'Kharkov') {
        defaultBaseUrl = '/kh';
    } else {
        defaultBaseUrl = '/';
    }

    const [ currentUrl, setCurrentUrl ] = React.useState(defaultBaseUrl);

    const onSetCurrentUrl = (name) => {
        setCurrentUrl(name);
    }

    const path = props.location.pathname.slice(3);

    return (
        <>
        <StateProvider>
            <Suspense fallback={<div>loading...</div>}>
                <Route
                path={currentUrl} 
                render={
                    (props) => <App {...props} onSetBaseName={onSetCurrentUrl} />
                }/>
                <Redirect exact from='/' to={currentUrl + path} />
            </Suspense> 
        </StateProvider>
        </>
    );
});

ReactDOM.render(
    <BrowserRouter>
        <Root/>
    </BrowserRouter>,
    document.getElementById('root')
);

reportWebVitals();